	import common from '../../../common/common.js'
	
	export default {
		data() {
			return {
				sjApiUrl:common.sjApiUrl,
				goodsid:0,
				shopInfo:[],		//用户信息
				goods:[],
				multiArray:[],		//商户类别
				catList:[],
				big_cat:[],
				big_cat_v:[],		//原始数据
				sub_cat:[],				
				sub_cat_v:[],		//原始数据
				multiIndex:[0,0],
				dateTimeArray: null,
				dateTime: null,
				dateTimeArray1: null,
				dateTime1: null,
				startYear: 2019,
				endYear: 2020,
				goods_id:0,
				goods_status:0,
				goods_cid:0,
				goods_picture:'',				
				attrArr:[],
				tagsArr:[],
				guigeArr:[],
				goods_tags:[],
				gg:[],
				dj:[],
				ggdanwei:['克(g)','千克(kg)','毫升(ml)','升(L)'],		//规格单位
				djdanwei:['斤','份','条','只','包','个'],		//单价单位
				popImport:false,
				key:'',
				goodsList:[],
				goods_wid:0,
				t_tagsArr:[],
			}
		},
		onLoad(option) {
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			that.shopInfo = shopInfo
			console.log(shopInfo)
			var id = option.id
			if(id > 0){
				that.goodsid = id
			}
			
			that.getGoodsInfo();
			//that.getMerchantCatList();

		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');
			
			that.getGoodsInfo();
			//that.getMerchantCatList();
			
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		
		methods: {
			bindMultiPickerChange(e) {
				var that = this
			    console.log('picker发送选择改变，携带值为', e.detail.value)
			    that.multiIndex = e.detail.value
			  },
			  bindMultiPickerColumnChange(e) {
				  var that = this
				  console.log(e)
			      console.log('修改的列为', e.detail.column, '，值为', e.detail.value);
			      var multiIndex = that.multiIndex
			      multiIndex[e.detail.column] = e.detail.value;
			      that.multiIndex = multiIndex
			      var multiArray = that.multiArray
				  if(e.detail.column == 0){
					  var sub_cat = []
					  for(var i=0;i<that.sub_cat_v[e.detail.value].length;i++){
						  console.log(that.sub_cat_v[e.detail.value])
					  	sub_cat.push(that.sub_cat_v[e.detail.value][i].cat_name)
					  }
					  multiArray[1] = sub_cat					  
				  }
				  that.multiArray = multiArray
				  that.multiIndex.splice(1, 1, 0)
				  that.multiIndex.splice(2, 1, 0)
				  
				  
			    },
			checkboxChange(e) {
				let that = this
                let values = e.detail.value;
					console.log(values);
                that.goods_tags = values
            },			
			//搜索商品
			inputkeys(e){
				let that = this
				let key = e.detail.value
				that.key = key
				that.getWarehouseGoodsList();
			},
			//商品列表
			getWarehouseGoodsList(){
				var that = this
				var shopInfo = uni.getStorageSync('shopInfo');
				//console.log(shopInfo);
				uni.request({
					url: common.sjApiUrl + 'merchant/getWarehouseGoodsList',
					method: 'GET',
					data: {
						key:that.key
					},
					success: res => {						
						that.goodsList = res.data.goodsList						
					},
					fail: () => {},
					complete: () => {}
				});
			},
			showImportToggle(){
				let that = this
				let popImport = that.popImport
				that.popImport = !popImport
			},
			//提示
			showTips(){
				uni.showToast({
					title: '请先导入商品基本数据',
					icon:'none',
					duration:1500
				});
			},
			//输入规格
			guigeinput(e){
				var that = this
				var v = e.detail.value
				var index = e.currentTarget.dataset.index
				var gindex = e.currentTarget.dataset.gindex
				that.guigeArr[index][gindex] = v

				console.log(that.guigeArr)
				//console.log(index)
				//console.log(gindex)
			},
			//选择规格单位
			pickerGGdanwei(e){
				var that = this
				var v = parseInt(e.target.value)
				var index = e.currentTarget.dataset.index
				var gindex = e.currentTarget.dataset.gindex
				that.gg[index] = v
				that.guigeArr[index][gindex] = that.ggdanwei[v]
				console.log(v)
				console.log(gindex)
				console.log(that.guigeArr);
			},
			
			//选择价格单位
			pickerDJdanwei(e){
				var that = this
				var v = parseInt(e.target.value)
				var index = e.currentTarget.dataset.index
				var gindex = e.currentTarget.dataset.gindex
				that.dj[index] = v
				
				that.guigeArr[index][gindex] = that.djdanwei[v]
				console.log(v)
				console.log(gindex)
				console.log(that.guigeArr);
			},
			//添加规格
			addGuige(){
				var that = this
				that.guigeArr.push(['','','','','']);
				that.gg.push(0);
				that.dj.push(0);
				console.log(that.gg);
				console.log(that.dj);
			},
			//删除属性
			delGuige(e){
				var that = this
				var index = e.currentTarget.dataset.index
				that.guigeArr.splice(index,1);
			},
			//输入属性
			attrinput(e){
				var that = this
				var v = e.detail.value
				var index = e.currentTarget.dataset.index
				that.attrArr[index] = v
			
				console.log(that.attrArr)
				//console.log(index)
				//console.log(gindex)
			},
			//添加属性
			addAttr(){
				var that = this
				that.attrArr.push('');
			},
			//删除属性
			delAtrr(e){
				var that = this
				var index = e.currentTarget.dataset.index
				that.attrArr.splice(index,1);
			},
			//选择商品状态
			tapStatus(e){
				var that = this
				that.goods_status = e.currentTarget.dataset.status
			},
			//上传图片
			uploadimg(){
				var that = this
				uni.chooseImage({
					success: (chooseImageRes) => {
						const tempFilePaths = chooseImageRes.tempFilePaths;
						
						uni.uploadFile({							
							url: common.sjApiUrl + 'merchant/uploadFile', //仅为示例，非真实的接口地址
							filePath: tempFilePaths[0],
							name: 'fileData',
							formData: {},
							success: (uploadFileRes) => {
								
								var data = JSON.parse(uploadFileRes.data)
								that.goods_picture = data.up_file
							}
						});
					},
					fail(res) {
						console.log(res)
					}
				});
			},
			//提交信息
			formSubmit: function (e) {
				var that = this
				var user
				var data = e.detail.value;
				var goods_title  = data.goods_title
				var goods_desc = data.goods_desc
				var goods_price = data.goods_price
				var goods_pre_price = data.goods_pre_price
				var goods_weight = parseFloat(data.goods_weight)
				var goods_cid = that.goods_cid
				goods_cid = goods_cid > 0 ? goods_cid : that.catList[that.index].cat_id
	
				var goods_fid = that.big_cat_v[that.multiIndex[0]]['cat_id']
				var goods_cid = that.sub_cat_v[that.multiIndex[0]][that.multiIndex[1]]['cat_id']
				
				// console.log(that.sub_cat_v,'that.sub_cat_v')
				// console.log(goods_fid,'goods_fid')
				// console.log(goods_cid,'goods_cid')
				// return false
				var goods_picture = that.goods_picture
				var goods_status = that.goods_status
				var goods_id = that.goods_id
				var guigeArr = JSON.stringify(that.guigeArr);
				var attrArr = JSON.stringify(that.attrArr);
				var goods_tags = that.goods_tags;
				var goods_wid = that.goods_wid
				
				
				if(goods_picture == ''){
					uni.showToast({ title:'请上传图片',duration:1000,icon:'none' })
					return false;
				}
				if(goods_title == ''){
					uni.showToast({ title:'请输入商品名称',duration:1000,icon:'none' })
					return false;
				}
				if(goods_price == ''){
					uni.showToast({ title:'请输入商品价格',duration:1000,icon:'none' })
					return false;
				}
				if(goods_cid == ''){
					uni.showToast({ title:'请选择商品类别',duration:1000,icon:'none' })
					return false;
				}
				
				uni.request({
					url: common.sjApiUrl + 'merchant/editGoods',
					method: 'GET',
					data: {
						goods_id:goods_id,
						goods_picture:goods_picture,
						goods_title:goods_title,
						goods_desc:goods_desc,
						goods_price:goods_price,
						goods_fid:goods_fid,
						goods_cid:goods_cid,
						goods_status:goods_status,
						goods_shopid:that.shopInfo.shop_id,
						goods_guige:guigeArr,
						goods_attr:attrArr,
					},
					success: res => {
						var data = res.data
						if(data.code == 0){
							uni.showToast({
								title: '操作成功',
								mask: false,
								duration: 1500
							});
							uni.reLaunch({
								url: '/pages/goods/goods',
							});
						}else{
							uni.showToast({
								title: data.msg,
								mask: false,
								duration: 1500
							});
							return false
						}
					},
					fail: () => {},
					complete: () => {}
				});
				
				
			},
			
			  
			//选择商品类k别
			pickerCat(e){
				var that = this
				var index = e.target.value
				that.index = index
				that.goods_cid = that.catList[index].cat_id
			},
			//获取商户商品类型
			getMerchantCatList(){
				var that = this
				var shopInfo = uni.getStorageSync('shopInfo');
				uni.request({
					url: common.sjApiUrl + 'merchant/getMerchantCatList',
					method: 'GET',
					data: {
						shop_id:shopInfo.shop_id
					},
					success: res => {
						console.log(res);
						// var goods_cid = that.goods_cid
						var data = res.data
						// var cat_list = data.cat_list
						// var category = []
						// for(var i=0;i<cat_list.length;i++){							
						// 	category.push(cat_list[i].cat_name)
						// }
						// that.category = category
						// that.catList = cat_list
						var multiArray = [];
						
						that.sub_cat_v = data.sub_cat
						that.big_cat_v = data.big_cat
						var sub_cat = []
						var big_cat = []
						for(var i=0;i<data.big_cat.length;i++){
							big_cat.push(data.big_cat[i].cat_name)
						}
						for(var i=0;i<data.sub_cat[0].length;i++){
							sub_cat.push(data.sub_cat[0][i].cat_name)
						}
						that.sub_cat = sub_cat
						that.big_cat = big_cat
						multiArray.push(big_cat)
						multiArray.push(sub_cat)
						that.multiArray = multiArray
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//商品信息
			getGoodsInfo(){
				var that = this
				if(that.goodsid == 0 ){
					that.getMerchantCatList();
					return false
				}
				uni.request({
					url: common.sjApiUrl + 'merchant/getGoodsInfo',
					method: 'GET',
					data: {
						goodsid:that.goodsid
					},
					success: res => {
						var data = res.data.goods
						console.log(res.data)
						that.goods = data
						that.goods_picture = data.goods_picture
						that.goods_status = data.goods_status
						that.goods_fid = data.goods_fid
						that.goods_cid = data.goods_cid
						that.goods_title = data.goods_title
						that.goods_desc = data.goods_desc
						that.goods_price = data.goods_price
						that.goods_weight = data.goods_weight
						that.goods_status = data.goods_status
						that.goods_id = data.goods_id
						that.guigeArr = data.goods_guige
						that.attrArr = data.goods_attr
						that.tagsArr = data.goods_tags
						that.t_tagsArr = data.goods_t_tags
						that.goods_wid = data.goods_wid
						var big_cat = res.data.big_cat
						var sub_cat = res.data.sub_cat
						var t_big_cat = []
						var t_sub_cat = []
						var subIndex = 0;
						var bigIndex = 0;
						console.log("goods_fid",data.goods_fid)
						console.log("goods_cid",data.goods_cid)
						for(var i=0;i<big_cat.length;i++){
							if(data.goods_fid > 0 && big_cat[i].cat_id == data.goods_fid){
								bigIndex = i;
							}
							t_big_cat.push(big_cat[i].cat_name)
						}
						
						console.log("t_big_cat",t_big_cat)
						for(var j=0;j<sub_cat[bigIndex].length;j++){
							if(data.goods_cid > 0 && sub_cat[bigIndex][j].cat_id == data.goods_cid){								
								subIndex = j
							}
							t_sub_cat.push(sub_cat[bigIndex][j].cat_name)
						}
						console.log("multiIndex",that.multiIndex)
						console.log("t_sub_cat",t_sub_cat)
						
						
						console.log("bigIndex",bigIndex)
						console.log("subIndex",subIndex)
						that.big_cat_v = big_cat
						that.sub_cat_v = sub_cat
						var multiArray = [];
						multiArray.push(t_big_cat)
						multiArray.push(t_sub_cat)						
						that.multiArray = multiArray
						that.multiIndex.splice(1, 1, 0)
						that.multiIndex.splice(2, 1, 0)
						that.multiIndex = [bigIndex,subIndex]
						console.log("multiArray",multiArray)
						console.log("multiIndex",that.multiIndex)
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//
			jumpUrl(e){
				var that = this
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url: url
				});
			},
			
		}
	}
